
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import backTop from '@/components/BackTop/backTop'
import ShowMore from '@/components/ShowMore/showMore'
export default{
components: {
XHeader,Collapse,ShowMore,backTop
},
mounted(){
this.params = this.$route.params;
this.getInfo();
},
data(){
return {
info:{},
params:{},
}
},
methods: {
getInfo(){

this.$ajax.post('',{
'MSH.1':'childrenExamService',
'MSH.2':"getById",
"DATA_TYPE": 0,//数据类型，新生儿访问时固定为0
'ID': this.params.ID,//ID
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res)
if(res){
this.info = res;
}
}).catch(error => {
console.log(error);

});
},
getCSQKString(BM) {
switch (BM) {
case 1:
return "顺产";
break;
case 2:
return "难产";
break;
case 3:
return "剖宫产";
break;
case 4:
return "双多胎";
break;
default:
return "";
break;
}
},
getXSEJBSCXMDMString(BM) {
switch (BM) {
case "1":
return "未进行";
break;
case "2":
return "检查均为阴性";
break;
case "3":
return "甲低";
break;
case "4":
return "苯丙酮尿症";
break;
case "5":
return "其他遗传代谢病";
break;
default:
return "";
break;
}
},
getMSString(BM) {
switch (BM) {
case 1:
return "红润";
break;
case 2:
return "黄染";
break;
case 3:
return "其他";
break;
default:
return "";
break;
}
}
},
}




